/* Styles for home page */

.chart-container {
	padding: 15px;
}

.linechart {
	margin: 20px;
}


.to-do {
	padding: 5px 20px;
	position: absolute;
	top: 50px;
	bottom: 0;
	left: 0;
	right: 0;
	ul {
		list-style-type: none;
		padding-left: 0;
		overflow: auto;
		position: absolute;
		top: 0;
		bottom: 55px;		
		li {
			margin-top: 5px;
			margin-bottom: 15px;
			.mdl-checkbox__box-outline {
				width: 20px;
				height: 20px;
			}
			.mdl-checkbox__label {
				margin-left: 5px;
			}
		}
	}
	form {
		display: inline-block;
		position: absolute;
		width: 80%;
	}
	button.btn {
		display: inline-block;
		float: right;
		width: 50px;
		min-width: 50px;
		height: 50px;
	}
	.bottom {
		position: absolute;
		bottom: 10px;
		right: 10px;
		left: 15px;
	}
}

#donut {
	 .c3-chart-arc {
	 	text {
	 		display: none;	 		
	 	}
	}
}



.weather-icon {
	width: 220px;
}
.temp {
	bottom: 30px;
	left: 30px;	
	font-size: 20px;
	line-height: 1.2;
}


/* Drag */ 

#drag2 {
	.panel-header:hover {
		cursor: move;
	}
}

@media (max-width: 839px) {
	.to-do, .to-do ul, .to-do .bottom, .to-do form, .weather-container {
		position: static;
	}
	.temp.absolute {
		position: relative;
	}
}